.Textflow{
    position: relative;
    height:200vh;
}
.textflowWrap{
    background-color: bisque;
    position:absolute;
    top:10em;
    left:50%;
    transform: translateX(-50%);
    &.red{
        top:15em;

        background-color: rosybrown;
        transform: translateX(-50%) rotate3d(3, 1.2, 0, 45deg);
        
        .strapShadow{
            background: linear-gradient(0deg, rgba(0,0,0,0.1) 10%, rgba(255,255,255,0.4));
        }
    }
    &.blue{
        top:25em;
        background-color: skyblue;

        transform: translateX(-50%) rotate3d(0, -1, -0.4, 45deg);


        .strapShadow{
        background: linear-gradient(rgba(0,0,0,0.2) 40%, rgba(255,255,255,0.3));
        }
    }
    &.yellow{
        top: 45em;
        background-color: yellow;
        text-transform: uppercase;
        transform: translateX(-50%) rotate3d(1, 3.2, 3, 45deg);
        .strapShadow{
            background: linear-gradient(rgba(0,0,0,0.1) 20%, rgba(255,255,255,0.3));
            }
    }
    &.black{
        top: 70em;
        background-color: black;
        color:#fff;
        text-transform: uppercase;
        transform: translateX(-50%) rotate3d(-4.7, 7.5, 0.2, -30deg);

    }
    .textStrap{
        font-size: 2em;
        padding: 0.5em 0;
        white-space: nowrap;
        font-weight: 700;
        span{
            display: inline-block;
        }
    
    }

    .strapShadow{
        width:100%;
        height:100%;
        position:absolute;
        top:0;
        left:0;
        background: linear-gradient(rgba(0,0,0,0.2) 40%, rgba(255,255,255,0.3));

    }


}
