.particleWrap{
    width:100%;
    height:calc(100vh - 3rem);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    .canvasParticle{
        position:absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background-color: #2d2d2d;
    }
}
