.clfix::after {
  display: block;
  content: "";
  clear: both;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #2d2d2d;
}

body {
  overflow-x: hidden;
}

header {
  width: 100%;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2d2d2d;
}

header a {
  color: #fff;
  padding: 0.5em;
}/*# sourceMappingURL=style.css.map */